@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  font-family: sans-serif;
  margin: 0;
  padding: 0; }

.flash-success,
.flash-warning,
.flash-error {
  border: 1px solid #ccc;
  display: block;
  left: 25%;
  padding: 30px;
  position: fixed;
  text-align: center;
  top: 15%;
  width: 50%;
  z-index: 1; }

.flash-success {
  background-color: #c7f282; }

.flash-warning {
  background-color: #f1e681; }

.flash-error {
  background-color: #f08181; }

.auth-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 15% auto 0;
  max-width: 600px; }
  .auth-form h3,
  .auth-form input {
    margin-bottom: 5%; }
  .auth-form button {
    align-self: flex-end;
    max-width: 200px; }

.container {
  background-size: cover; }

.wrapper {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }

.main {
  height: 95vh;
  background: #fff;
  width: 92%;
  margin: 0 auto;
  display: flex; }

.title-container {
  height: 100vh;
  background: url(/weatherforecast/static/media/bg.bcdd7f60.jpg) center center no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: left;
  color: #000;
  width: 40%;
  padding-left: 30px;
  padding-right: 50px;
  padding-right: 20px; }

.chart-container {
  background-size: cover;
  color: #fff; }

.form-container {
  height: 148vh;
  padding-top: 50px;
  padding-left: 30px;
  margin-bottom: 100px;
  overflow: hidden;
  color: #000;
  background: url(/weatherforecast/static/media/skyback.f235fab6.jpg) center center no-repeat;
  background-size: cover; }

.profileform {
  color: black; }

input[type="text"]:not(.profileinput) {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #fff;
  width: 30%;
  padding-bottom: 4px;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px; }

input[type="text"]:not(.profileinput) {
  outline: none; }

input:-webkit-autofill:not(.profileinput) {
  -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
  -webkit-text-fill-color: #fff !important; }

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #f16051;
  color: #fff;
  font-weight: 100; }

button:active {
  outline: none; }

.weather__info {
  width: 60%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px; }

.weather__key {
  color: #f16051;
  border-bottom: solid 2px rgba(255, 255, 255, 0.06);
  padding: 20px 0 20px 0;
  font-weight: 400; }

.weather__key:last-child {
  border: 0; }

.weather__value {
  color: #fff;
  font-weight: 200; }

.weather__error {
  color: #f16051;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200; }

.weather__infoleft {
  font-size: 20px;
  letter-spacing: 2px;
  margin-top: -260px;
  background: #FFEFBA;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #FFFFFF, #FFEFBA);
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(bottom, #FFFFFF, #FFEFBA);
  background: linear-gradient(to top, #FFFFFF, #FFEFBA);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  opacity: .6;
  padding-left: 20px;
  padding-top: 10px; }

.weather__keyleft {
  color: #f16051; }

.weather__valueleft {
  color: #000;
  font-weight: 100; }

.weather__condition {
  color: red;
  font-size: 36px; }

.weather__valueleftdetail {
  color: #3e4042; }

.weather__error {
  color: #f16051;
  font-size: 20px;
  letter-spacing: 1px; }

.profileindex {
  -webkit-transition: box-shadow .3s;
  transition: box-shadow .3s;
  width: 50%;
  height: auto;
  margin: 50px;
  border-radius: 10px;
  border: 1px solid #ccc;
  background: #bee5eb;
  float: left;
  padding: 20px;
  margin-left: 20%; }

.profileindex:hover {
  box-shadow: 0 0 11px rgba(33, 33, 33, 0.2); }

.main-header {
  align-items: center;
  border-bottom: 1px solid #bbb;
  display: flex;
  height: 100px;
  padding: 0 20px;
  font-weight: 200;
  background: #2980B9;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #FFFFFF, #6DD5FA, #2980B9);
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(right, #FFFFFF, #6DD5FA, #2980B9);
  background: linear-gradient(to left, #FFFFFF, #6DD5FA, #2980B9);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }
  .main-header a,
  .main-header span {
    font-size: 1.2em;
    margin: 0 15px; }

.main-header > nav {
  align-items: center;
  display: flex;
  flex-grow: 1;
  justify-content: flex-end; }

@media (max-width: 600px) {
  .main-header {
    flex-direction: column;
    font-size: 0.7em;
    height: auto;
    padding: 10px; }
    .main-header h1 {
      font-size: 2.5em; } }

@media (max-width: 450px) {
  .main-header > nav {
    flex-direction: column;
    width: 100%; }
    .main-header > nav a,
    .main-header > nav span {
      border: 1px solid #bbb;
      margin-bottom: 5px;
      text-align: center;
      width: 80%; } }

